/* Swipe Split Layout Styles */
.swipe-split-section {
  display: flex;
  align-items: center;
  min-height: 100vh;
  gap: 60px;
  position: relative;
  overflow: hidden;
  padding: 0px 40px;
}

.swipe-split-left {
  flex: 0 0 35%;
  position: relative;
  overflow: visible;
  cursor: pointer;
  user-select: none;
}

.swipe-split-right-positioned {
  order: 2;
  flex: 0 0 35%;
}

.swipe-circle-large {
  width: 600px;
  height: 600px;
  border-radius: 50%;
  background: linear-gradient(135deg, #E85A8A 0%, #FD2B7B 100%);
  box-shadow: 0 20px 80px rgba(253, 43, 123, 0.5),
              0 0 100px rgba(253, 43, 123, 0.3);
  position: relative;
  left: -200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  transition: all 0.3s ease;
}

.swipe-circle-large:hover {
  transform: scale(1.05);
  filter: brightness(1.1);
}

.swipe-circle-large:active {
  transform: scale(1.02);
}

.swipe-circle-messaging {
  background: linear-gradient(135deg, #FF7158 0%, #FD2B7B 100%);
  box-shadow: 0 20px 80px rgba(255, 113, 88, 0.5),
              0 0 100px rgba(255, 113, 88, 0.3);
  left: auto;
  right: -200px;
}

.circle-icon {
  font-size: 120px;
  margin-bottom: 30px;
  filter: drop-shadow(0 8px 16px rgba(0,0,0,0.3));
}

.circle-label {
  font-size: 48px;
  font-weight: 700;
  text-transform: capitalize;
  letter-spacing: 2px;
}

.swipe-hint {
  font-size: 18px;
  margin-top: 30px;
  opacity: 0.9;
  font-weight: 500;
}

.swipe-split-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 40px;
  z-index: 2;
}

.swipe-split-right-content {
  flex: 1;
  position: relative;
  z-index: 2;
}

.swipe-split-left-content {
  order: 1;
  flex: 1;
  position: relative;
  z-index: 2;
}

.chart-slide {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transition: opacity 0.6s ease, transform 0.6s ease;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.chart-active {
  opacity: 1;
  transform: rotateY(0deg) translateZ(0);
  position: relative;
  animation: flipIn 0.6s ease-out forwards;
}

.chart-hidden {
  opacity: 0;
  transform: rotateY(90deg) translateZ(-100px);
  pointer-events: none;
}

@keyframes flipIn {
  0% {
    opacity: 0;
    transform: rotateY(-90deg) translateZ(-100px);
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
    transform: rotateY(0deg) translateZ(0);
  }
}

.chart-card-modern {
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(253, 43, 123, 0.2);
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.chart-card-modern:hover {
  box-shadow: 0 12px 40px rgba(253, 43, 123, 0.2);
  transform: translateY(-2px);
}

.chart-title-modern {
  font-size: 22px;
  font-weight: 600;
  background: linear-gradient(135deg, #FD2B7B 0%, #FF7158 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 15px;
}

.chart-description-modern {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
  margin-bottom: 25px;
}

.chart-wrapper {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease, transform 0.6s ease;
}

.chart-wrapper.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Key Finding Cards */
.key-findings-row {
  display: flex;
  gap: 20px;
  margin-bottom: 30px;
  transition: opacity 0.6s ease, transform 0.6s ease;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
}

.key-findings-row .card {
  flex: 1;
  min-width: 0;
  animation: cardFlipIn 0.6s ease-out forwards;
}

@keyframes cardFlipIn {
  0% {
    opacity: 0;
    transform: rotateX(-90deg) translateY(-30px);
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
    transform: rotateX(0deg) translateY(0);
  }
}

.findings-active {
  opacity: 1;
  transform: translateY(0);
}

.findings-hidden {
  opacity: 0;
  transform: rotateX(90deg) translateY(30px);
  pointer-events: none;
  position: absolute;
}

.finding-card-split {
  background: linear-gradient(135deg, rgba(253, 43, 123, 0.1), rgba(255, 113, 88, 0.1));
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 20px;
  border: 1px solid rgba(253, 43, 123, 0.2);
  transition: all 0.3s ease;
  text-align: center;
}

.finding-card-split:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(253, 43, 123, 0.2);
}

.finding-icon-split {
  font-size: 36px;
  margin-bottom: 12px;
}

.finding-title-split {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 8px;
  color: #424242;
}

.finding-description-split {
  font-size: 13px;
  line-height: 1.5;
  color: #666;
}

/* Responsive Design */
@media (max-width: 968px) {
  .swipe-split-section {
    flex-direction: column;
    padding: 60px 20px;
    gap: 40px;
  }

  .swipe-split-left {
    flex: 0 0 auto;
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .swipe-circle-large {
    width: 400px;
    height: 400px;
    left: 0;
  }

  .circle-icon {
    font-size: 80px;
  }

  .circle-label {
    font-size: 36px;
  }

  .swipe-split-right {
    width: 100%;
  }

  .key-findings-row {
    flex-direction: column;
  }
}

@media (max-width: 600px) {
  .swipe-circle-large {
    width: 300px;
    height: 300px;
  }

  .circle-icon {
    font-size: 60px;
  }

  .circle-label {
    font-size: 28px;
  }

  .chart-card-modern {
    padding: 20px;
  }

  .chart-title-modern {
    font-size: 18px;
  }

  .chart-description-modern {
    font-size: 14px;
  }
}

